//angular
import { Component, OnInit, Input, OnDestroy } from '@angular/core';
//models
import { CharModel } from 'src/app/shared/models/char.model';
import { ConfigModel } from 'src/app/shared/models/services/config/config.model';
import { PlayerModel } from 'src/app/shared/models/player.model';
//services
import { DbService } from 'src/app/shared/services/db.service';
import { ConfigService } from 'src/app/shared/services/config.service';
//other
import { ParseToStringArrHelper } from 'src/app/shared/helpers/ParseToStringArr-helper';
import { RandomIntInclusiveHelper } from 'src/app/shared/helpers/RandomIntInclusive-helper';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-board',
  templateUrl: './board.component.html',
  styleUrls: ['./board.component.scss']
})
export class BoardComponent implements OnInit, OnDestroy {
  isLoading = true;
  badTry: number = 0; //ilość złych prób wyboru znaku
  roundResult: boolean | null = null; //Czy graz grający przegrał czy wygrał runde
  gameResult: PlayerModel | null = null; //który gracz wygrał całą rozgrywkę
  roundScore: number | null;  // który gracz wygrał rundę
  roundNr: number = 1;
  config: ConfigModel;

  password: string | null; //tymczasowe hasło
  passwordArr: string[] = []; 
  hidePasswordArr: string[] = [];
  playerChoosingPassword: PlayerModel;   

  startTime: number | null = null;
  stopTimer: boolean =false;

  player1: PlayerModel = new PlayerModel(1, "Player 1", true);
  player2: PlayerModel = new PlayerModel(2, "Player 2", false);
  player: PlayerModel | null = null;
  
  keyboard: CharModel[] = [];

  $res: Subscription;
  constructor(private configService: ConfigService, private dbService: DbService) { 
 
  }

  ngOnInit(): void {
    this.$res = this.configService._configData.subscribe(x => {
      this.config = x;
    });
    
    this.initAlphabetObjArr();

    if(this.config.multiPlrGame)
    {
      this.player = this.player1;
    }
    else
    {
      this.generatePasswordForSinglePlayerGame();
    }
    setTimeout(() => {
      this.isLoading = false;
    }, 500);
  }

  ngOnDestroy(): void{
    if(this.$res){
      this.$res.unsubscribe();
    }
  }

  //#region Init board

  generatePasswordForSinglePlayerGame(){
    if(this.config.onlyWord){
      this.password = this.dbService.passwords[RandomIntInclusiveHelper.getRandomIntInclusive(0,this.dbService.passwords.length-1)];
    }
    else{
      this.password = this.dbService.sentences[RandomIntInclusiveHelper.getRandomIntInclusive(0,this.dbService.sentences.length-1)];
    }
    this.passwordArr = ParseToStringArrHelper.parseToStringArr(this.password);
    this.initHidePasswdArr();
  }

  initAlphabetObjArr(){
    this.keyboard = [];
    let alphabet = this.dbService.alphabet;
    for(let i = 0; i< alphabet.length; i++){
      this.keyboard.push(new CharModel(i, alphabet[i], false, false));
    }
  }

  initHidePasswdArr(){
    this.hidePasswordArr = [];
    this.passwordArr.forEach(x => {
      if(x === ' '){
        this.hidePasswordArr.push('.');
        //this.charOfHitCount++;
      }
      else{
        this.hidePasswordArr.push('-');
      }
    });
  }
  //#endregion

  passwordLength(password: string[]): number{
    let res: number = 0;
    for(let i=0; i< password.length; i++){
      if(password[i] !== ' '){
        res++;
      }
    }
    return res;
  }
  //#region Keyboard
  selectChar(char: CharModel){
    if(char.selected){  //czy znak był już wybrany
      return false;
    }
    this.roundResult = null
    let isInPasswd = false;

    for(let i = 0; i < this.passwordArr.length; i++){ //sprawdza czy znak jest w haśle i nadpisuje go na swoim miejscu w ukrytym haśle
      if(this.passwordArr[i] === char.char){
        this.hidePasswordArr[i] = char.char;
        isInPasswd = true;
      }
    }

    this.keyboard.find(x => x.char === char.char).selected = true;
    this.keyboard.find(x => x.char === char.char).isInPasswd = isInPasswd;

    if(isInPasswd){
      if(this.nrOfStrikeCharInPassword === this.passwordLength(this.passwordArr)){
        if(this.config.multiPlrGame){
          if(this.roundNr < this.config.roundCount)
          {
            this.roundResult = true;
            this.setResultRoundMultiPlayerGame();
          }
          else
          {
            if(this.player1.isPlaying){ 
              this.roundScore = 1;
            }
            if(this.player2.isPlaying){
              this.roundScore = 2;
            }
          }
          this.stopTime(false);
        }
        else{
          this.roundResult = true;
          this.stopTime(false);
        }
      }
    }
    else{
      this.badTry++;
      if(this.badTry === 9){
        if(!this.config.multiPlrGame){
            this.roundResult = false; 
        }
        else{
            if(this.roundNr < this.config.roundCount)
            {
              this.roundResult = false;
              this.setResultRoundMultiPlayerGame();
            }
            else{
              if(this.player1.isPlaying){ 
                this.roundScore = 1;
              }
              if(this.player2.isPlaying){
                this.roundScore = 2;
              }
            }
            this.stopTime(false);
        }    
      }
    }
  }

  //#endregion

  setPassword(password: string){
    this.password = password;
    this.passwordArr = ParseToStringArrHelper.parseToStringArr(this.password);
    this.initHidePasswdArr();
    this.playerChoosingPassword = null;
    this.startTimer();
  }

  setResultRoundMultiPlayerGame(){
    if(this.player1.isPlaying){ 
      this.roundScore = this.roundResult ? 1 : 2;
    }
    if(this.player2.isPlaying){
      this.roundScore = this.roundResult ? 2 : 1;
    }
  }

  //#region plr-name dialog
  next(player: PlayerModel){
    if(player.id === 1){
      this.player1.name = player.name;
      this.player = this.player2;
    }
    if(player.id === 2){
      this.player2.name = player.name;
      this.player = null;

      this.playerChoosingPassword = this.player1;  //wyswietl wybieranie hasla
    }
  }
  //#endregion

  nextRound(){
    this.clearBoard();
    if(this.isMultiPlayerGame){
      if(this.roundNr < this.config.roundCount){
        this.choosePasswordForOpponent();
      }
      this.roundScore = null; 
      this.changePlayerPlaying();
    }
    else{
      this.generatePasswordForSinglePlayerGame();
    }
    this.roundNr++;

  }

  choosePasswordForOpponent(){
    if(this.player1.isPlaying){
      this.playerChoosingPassword = this.player2;  //wybiera haslo dla rpzeciwnika
    }
    else{
      this.playerChoosingPassword = this.player1;  //wybiera haslo dla rpzeciwnika
    }
  }

  clearBoard(){
    this.roundResult = null;
    this.badTry = 0;
    this.roundScore =null;
    this.initAlphabetObjArr();
    this.initHidePasswdArr();
  }

  changePlayerPlaying(){
    this.player1.isPlaying = !this.player1.isPlaying; 
    this.player2.isPlaying = !this.player2.isPlaying;
  }

  setWinnerPlayer(player: PlayerModel){
    setTimeout(() => {
      this.gameResult = player;
        }, 1);

  }

  //#region Timer
  stopTime(endTime:boolean){
    this.startTime = null;
    this.stopTimer = true;
    if(endTime){
      if(this.roundNr < this.config.roundCount)
      {
        this.roundResult = false;
      }
      this.setResultRoundMultiPlayerGame();
    }
  }

  startTimer(){
    this.stopTimer = false;
    this.startTime = this.config.sec;
  }
  //#endregion

  get playerPlaying(): string{
    return this.player1.isPlaying ? this.player1.name : this.player2.name;
  }

  get isMultiPlayerGame(): boolean{
    return this.config.multiPlrGame;
  }

  get nrOfStrikeCharInPassword(): number{
    let res = this.hidePasswordArr.length;
    this.hidePasswordArr.forEach(x => (x === "." || x === "-") ? res--: res);
    return res;
  }

}