//angular
import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
//models
import { PlayerModel } from 'src/app/shared/models/player.model';
//other
import { faWindowClose, faSadTear, faLaughBeam } from '@fortawesome/free-solid-svg-icons';

@Component({
  selector: 'app-dialog-result',
  templateUrl: './dialog-result.component.html',
  styleUrls: ['./dialog-result.component.scss']
})
export class DialogResultComponent implements OnInit {
  //#region icons
    faWindowClose = faWindowClose;
    faSadTear = faSadTear;
    faLaughBeam = faLaughBeam;
  //#endregion

  display: boolean = false;
  _roundResult: boolean;
  _gameResult: PlayerModel | null;

  constructor() { }

  @Input()
  password: string;
  
  @Input()
  isMultiPlayerGame;

  @Input()
  set roundResult(val: boolean | null){
    if(val === true || val === false){
      this._roundResult = val;
      this.display = true;
    }
  }

  @Input()
  set gameResult(val: PlayerModel){
    if(!!val)
    {
      this._gameResult = val;
      this.display = true;
    }
  }

  @Output() 
  nextRound = new EventEmitter();

  ngOnInit(): void {
  }

  onNextRound(){
    this.display = false;
    this.nextRound.emit(null);
  }

}
