//angular
import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
//models
import { PlayerModel } from 'src/app/shared/models/player.model';
//other
import { faCheckCircle, faTimesCircle, faUser } from '@fortawesome/free-solid-svg-icons';

@Component({
  selector: 'app-score',
  templateUrl: './score.component.html',
  styleUrls: ['./score.component.scss']
})
export class ScoreComponent implements OnInit {  
  //#region icons
    faCheckCircle = faCheckCircle;
    faTimesCircle = faTimesCircle;
    faUser = faUser;
  //#endregion

  private roundNr: number = 0;

  @Input()
  roundCount: number;
    
  @Input()
  player1: PlayerModel;

  @Input()
  player2: PlayerModel;

  @Input()
  set roundScore(roundWinner:number){
    if(roundWinner === 1 || roundWinner === 2){
      roundWinner === 1 ? this.player1Score[this.roundNr] = true: this.player1Score[this.roundNr] = false;
      roundWinner === 2 ? this.player2Score[this.roundNr] = true: this.player2Score[this.roundNr] = false;
      this.roundNr++;
      if(this.roundNr ===this.roundCount){
        let player1WinRounds = 0;
        let player2WinRounds = 0;
        this.player1Score.forEach(x => x === true? player1WinRounds++: player1WinRounds);
        this.player2Score.forEach(x => x === true? player2WinRounds++: player2WinRounds);
        if(player1WinRounds > player2WinRounds)
        {
            this.winner.emit(this.player1);
        }
        if(player2WinRounds > player1WinRounds)
        {
            this.winner.emit(this.player2);
        }
        if(player1WinRounds === player2WinRounds){
          let drawPlayer = new PlayerModel(-1,'Draw');
          this.winner.emit(drawPlayer);
        }
      }
    }
  }

  @Output()
  winner = new EventEmitter();

  player1Score: Array<any>;
  player2Score: Array<any>;;

  constructor() { }

  ngOnInit(): void {
    this.player1Score = new Array<Boolean>(this.roundCount).fill(null);  
    this.player2Score = new Array<Boolean>(this.roundCount).fill(null);  
  }

}
