//angular
import { Injectable } from '@angular/core';
//models
import { ConfigModel } from '../models/services/config/config.model';
import { BehaviorSubject, ReplaySubject, Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class ConfigService {

  _configData = new ReplaySubject<ConfigModel>(1);
  dataStore: {
    configData: ConfigModel | null,
  }  

  constructor(){
    this.clear();
    this.dataStore.configData = new ConfigModel(true,11,true,10,true);
    this.refreshConfig(this.dataStore.configData);
  }

  clear() {
    this.dataStore = {
      configData: null
    }
  }
  set config(conf: ConfigModel){
    this.dataStore.configData = conf;
    this.refreshConfig(this.dataStore.configData);  }

  set typeOfGame(multiPlrGame: boolean){
    this.dataStore.configData.multiPlrGame = multiPlrGame;
    this.refreshConfig(this.dataStore.configData);
  }

  get configData(): Observable<ConfigModel>{
    return this._configData.asObservable();
  }

  refreshConfig(config: ConfigModel){
    this._configData.next(config);
  }
  
}
