//models
import { PlayerModel } from '../../player.model';

export class ConfigModel{
    constructor(useTime: boolean = false, sec: number | null = 60, onlyWord: boolean = true, 
        roundCount: number | null = 5, multiPlrGame: boolean = true){
            
        this.useTime = useTime;
        this.sec = sec;
        this.onlyWord = onlyWord;
        this.roundCount = roundCount;
        this.multiPlrGame = multiPlrGame;
    }

    useTime: boolean;
    sec: number;
    onlyWord: boolean;
    roundCount: number;
    multiPlrGame: boolean;
    plr1: PlayerModel;
    plr2: PlayerModel;
}