//angular
import { Component, OnInit, DoBootstrap, OnDestroy } from '@angular/core';
import { FormGroup, Validators, FormBuilder } from '@angular/forms';
//services
import { ConfigService } from 'src/app/shared/services/config.service';
//models
import { ConfigModel } from 'src/app/shared/models/services/config/config.model';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-settings',
  templateUrl: './settings.component.html',
  styleUrls: ['./settings.component.scss']
})
export class SettingsComponent implements OnInit, OnDestroy {

  selectedValue: string = "word";
  isDisable = true;
  config: ConfigModel;
  form: FormGroup;

  succesAlert:boolean = false;
  $res: Subscription;

  constructor(private configService: ConfigService, private fb: FormBuilder) { }

  ngOnInit(): void {
    this.$res = this.configService._configData.subscribe(x => {
      this.config = x;
    });
    this.selectedValue = this.config.onlyWord? "word": "sentence";
    this.createForm();
  }

  ngOnDestroy(): void{
    if(this.$res){
      this.$res.unsubscribe();
    }
  }

  createForm(){
    this.form = this.fb.group({
      timeCb: [this.config.useTime, Validators.required],
      timeVal: [this.config.sec , [Validators.required, Validators.min(10)]],
      rounds: [this.config.roundCount, [Validators.required, Validators.min(2)]],
    });
  }

  toggleFormTimeVal(){
    this.isDisable = !this.isDisable;
    if(Boolean(this.form.value.timeCb)){
      this.form.controls.timeVal.setValidators([Validators.required]);
    }
    else{
      this.form.controls.timeVal.clearValidators();
    }
    this.form.controls.timeVal.updateValueAndValidity();
  }

  onSubmit(){

    if(this.form.valid){
      let conf: ConfigModel= new ConfigModel(
        Boolean(this.form.value.timeCb),
        Number(this.form.controls.timeVal.value),
        this.selectedValue === "word" ? true : false,
        Number(this.form.controls.rounds.value)
        );

        this.succesAlert = true;
        setTimeout(() => {
          this.succesAlert = false;
        }, 1000);

      this.configService.config = conf;
    }
  }

  get error(): boolean{
    return !this.form.valid;
  }

  get timeVal() {
    return this.form.get('timeVal');
  }

  get rounds() {
    return this.form.get('rounds');
  }

}
