//amgular
import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-timer',
  templateUrl: './timer.component.html',
  styleUrls: ['./timer.component.scss']
})
export class TimerComponent implements OnInit {

  constructor() { }

  private interval;
  sec: number = 0;

  @Input()
  set stopTimer(val: boolean){
    if(val){
      clearInterval(this.interval);
    }
  }


  @Input()
  set startTimer(sec: number){
    if(sec > 10){
      this.sec = sec;
      this.interval = setInterval(() => {
        this.countTime(this.sec); 
      }, 1000);
    }
  }

  @Output()
  stopTime = new EventEmitter();

  ngOnInit(): void {
  }

  countTime(sec: number){
    this.sec--;
    if(this.sec === 0)
    {
      clearInterval(this.interval);
      this.onStopTime();
    }
  }

  onStopTime(){
    this.stopTime.emit(true);
  }

  get timerColorClass(): string{
    if(this.sec < 10 && this.sec > 3){
      return 'color-orange';
    }
    if(this.sec <= 3 && this.sec > 0){
      return 'color-red';
    }
    return '';
  }
}
