//angular
import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
//models
import { PlayerModel } from 'src/app/shared/models/player.model';
//other
import { faWindowClose } from '@fortawesome/free-solid-svg-icons';

@Component({
  selector: 'app-password-dialog',
  templateUrl: './password-dialog.component.html',
  styleUrls: ['./password-dialog.component.scss']
})
export class PasswordDialogComponent implements OnInit {
//#region icons
faWindowClose = faWindowClose;
//#endregion
errorAlert: string = "";
display: boolean = false;
password: string | null;
_player: PlayerModel;

constructor() { }

@Input()
onlyWord: boolean;

@Input()
set player(val: PlayerModel){
  if(!!val)
  {
    this._player = val;
    this.password = "";
    this.display = true;
  }
}

@Output() 
setPassword = new EventEmitter();

ngOnInit(): void {
}

next(){
  if(this.checkPassword()){
    this.setPassword.emit(this.password);
    this.display = false;
  }
  else{
    this.errorAlert = "Wrong password, You can't use whitespace.";
  }
}

get error(): string{
  if(this.password.length < 1){
    this.errorAlert = "Easy password. Try a bit harder. :)";
  }
  return this.errorAlert;
}


checkPassword(): Boolean{
  if(this.onlyWord){
    for(let i=0; i < this.password.length; i++){
      if(this.password.charAt(i) === " ")
        return false;
     }
     return true;
  }else{
    return true;
  }
}
}
