export class CharModel{
    constructor(id: number, char: string, selected: boolean, isInPasswd: boolean){
        this.id = id;
        this.char = char;
        this.selected = selected;
        this.isInPasswd = isInPasswd;
    }
    
    id: number;
    char: string;
    selected: boolean;
    isInPasswd: boolean;
}
  